[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html, body {
    background-color: var(--background);
    color: var(--color);
}

.select {
    background-color: inherit;
    color: inherit;
}

